<script>
import ModalCal from "@/components/ModalCal";
import json_man from "@/assets/data/man.json";
import json_woman from "@/assets/data/woman.json";
//import csv from "csvtojson";

export default {
  name: "DeathRiskCal",
  components: {
    ModalCal
  },
  data() {
    return {
      userAge: null,
      userSex: 0,
      comorbilidades: {
        opt01: false,
        opt02: false,
        opt03: false,
        opt04: false,
        opt00: false
      },
      cancer: 5.399,
      diabetes: 2.206,
      cardiovascular: 2.14,
      hypertension: 1.878,
      start_result: "Si se infecta, su riesgo de morir por COVID-19 es",
      body_result: 0,
      end_result: "???"
    };
  },
  mounted() {
    this.loader();
  },
  methods: {
    loader: () => {
      window.$("section").fadeIn();
    },
    calcular() {
      if (parseInt(this.userAge) >= 20 && parseInt(this.userAge) <= 100) {
        //console.log("edad => " + this.userAge);
        //console.log("Sexo => " + this.userSex);
        //console.log("Comobilidad => ", this.comorbilidades);
        let age = this.userAge ? this.userAge : 1;
        var listAge = this.userSex === "1" ? json_man : json_woman;
        let n = this.comorbilidades.opt01 ? this.cancer : 1;
        let t = this.comorbilidades.opt02 ? this.diabetes : 1;
        let o = this.comorbilidades.opt03 ? this.cardiovascular : 1;
        let i = this.comorbilidades.opt04 ? this.hypertension : 1;
        let valInfo = listAge[age];
        //console.log(valInfo);
        let result_val =
          (valInfo / 100) * (n - 1 + 1 + (t - 1) + (o - 1) + (i - 1));
        let valN = Math.round(1e5 * result_val);
        let valI = 100 * result_val;
        this.body_result = this.format(valI, ".", 2) + "%";
        this.end_result = "En el grupo de "
          .concat(1e5, " personas como t, que se infectaron, ")
          .concat(valN, " muriern");
      } else {
        this.body_result = "?%";
        this.end_result = "No hay suficientes datos para este grupo de edad.";
      }
    },
    format(n, sep, decimals) {
      sep = sep || "."; // Default to period as decimal separator
      decimals = decimals || 2; // Default to 2 decimals
      var info = 0;
      info = n.toLocaleString("en-US").split(sep)[0];
      info = info + sep + n.toFixed(decimals).split(sep)[1];
      return info;
    }
  }
};
</script>
