<template>
  <section class="notices mt-lg-5 py-lg-5 none-v bg-light">
    <div class="container">
      <div class="row">
        <div class="col">
          <div class="my-4 pt-2">
            <h2 class="display-4 text-center text-primary font-weight-bold">
              Notices
            </h2>
          </div>
        </div>
      </div>
      <div class="row d-flex justify-content-center">
        <div
          v-for="(obj, index) in news"
          :key="index"
          class="col-11 col-sm-10 col-md-6 col-lg-4 col-lx-3 mb-3 mt-3 "
        >
          <News v-bind:obj="obj" v-bind:code="index" />
        </div>
      </div>
      <div v-if="see_more" class="row mb-5">
        <div class="col mt-5 mb-3 text-center">
          <router-link
            to="/contribution"
            class="mr-2 btn btn-secondary btn-radius-bottom-right mb-0"
            href="#"
          >
            <span class="mn-top">See more news</span>
          </router-link>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";
import News from "@/components/News";
import csv from "csvtojson";

export default {
  name: "Notices",
  components: {
    News
  },
  data() {
    return {
      loading: false,
      see_more: true,
      news: []
    };
  },
  created() {
    this.loadNews();
  },
  mounted() {
    this.loader();
  },
  methods: {
    loader: () => {
      window.$("section").fadeIn();
    },
    loaderNotices() {
      var pathname = window.location.pathname;
      this.see_more = true;

      if (pathname == "/" || pathname == "") {
        this.see_more = false;
        let newsHome = [];
        newsHome[0] = this.news[0];
        newsHome[1] = this.news[1];
        this.news = [];
        this.news = newsHome;
        this.viewMore();
      }
    },
    viewMore() {
      this.news.push({
        title: "The Covid-19 Colombia Project",
        desc:
          "We have a latent obligation to report and transmit relevant events and news to our users.",
        source: "The Covid-19 Colombia Project",
        source_url: "See more news",
        source_ico: null,
        image: require("@/assets/images/news/_default.png"),
        url: "https://coviddataproject.com",
        bg_hover: "ani-bg-secondary",
        btn_bg: "bg-secondary",
        btn_title: "bg-primary",
        btn_desc: "text-white"
      });
    },
    loadNews() {
      //https://github.com/MrDaza/api-default/blob/master/news/news.csv
      //https://raw.githubusercontent.com/MrDaza/api-default/blob/master/news/news.csv
      this.loading = true;
      var url =
        "https://raw.githubusercontent.com/MrDaza/api-default/master/news/news.csv";
      axios(url)
        .then(request => {
          const html = request.data;
          //console.log("HTML:", html);
          var status = html.status;
          var statusText = html.statusText;
          console.log("status=" + status + " || statusText=" + statusText);
          if (status == "200" || statusText == "OK") {
            csv({
              noheader: false,
              output: "json"
            })
              .fromString(html)
              .then(jsonObj => {
                this.news = jsonObj.reverse();
                this.loaderNotices();
              });
            this.loading = false;
          } else if (!status || !statusText) {
            csv({
              noheader: false,
              output: "json"
            })
              .fromString(html)
              .then(jsonObj => {
                this.news = jsonObj.reverse();
                this.loaderNotices();
              });
            this.loading = false;
          } else {
            //console.log("Error");
            this.loading = true;
          }
        })
        .catch(error => {
          console.log("Error =>", error);
          this.loading = true;
        });
    }
  }
};
</script>
