<script>
import axios from "axios";
export default {
  name: "DailyIndicators",
  data() {
    return {
      loading: false,
      indicators: {
        Report_date: null,
        Active: null,
        Confirmed: null,
        Deaths: null,
        Recovered: null,
        House: null,
        Hospital: null,
        ICU: null,
        People_Tested: null,
        Hospital_Beds_Utilisation: null,
        ICU_Beds_Utilisation: null,
        People_Hospitalized: null,
        Total_Hospitalization_Rate: null,
        Mild_Rate: null,
        Severe_Rate: null,
        Critical_Rate: null,
        Lethality_Rate: null,
        Recovery_Rates: null,
        Active_Rate: null,
        Testing_Rate: null,
        Mortality_Rate: null,
        Morbidity_Rate: null,
        Ro_Index: null,
        Forecasts: null,
        Last_Update: null
      }
    };
  },
  mounted() {
    this.loadDailyIndicators();
  },
  methods: {
    format(n, sep, decimals) {
      sep = sep || "."; // Default to period as decimal separator
      decimals = decimals || 2; // Default to 2 decimals
      var info = 0;
      info = n.toLocaleString("en-US").split(sep)[0];
      info = info + sep + n.toFixed(decimals).split(sep)[1];
      return info;
    },
    loadDailyIndicators() {
      this.loading = true;
      let url =
        "https://us-central1-covid19-data-project.cloudfunctions.net/globalIndicators?opt=last";
      axios(url)
        .then(request => {
          const html = request.data;
          var status = html.status;
          var statusText = html.statusText;
          if (status == "200" || statusText == "OK") {
            this.indicators = html.data;
            this.loading = false;
          } else {
            console.log("Error");
            this.loading = true;
          }
        })
        .catch(() => {
          this.loading = true;
        });
    }
  }
};
</script>

